.header {
  margin: auto;
  background: #5C5C5C;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.logo {
  background-image: url(/static/media/logo.6a56c027.svg);
}

.header__profile {
  margin: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.header__registration {
  margin: auto;
  padding: 0;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  color: #FFFFFF;
  border: 0px;
  text-decoration: none;
}

.header__login {
  margin: auto;
  background: #2BE080;
  border-radius: 3px;
  border: none;
  color: #000000;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}

@media screen and (min-width: 320px) {
  .header {
    max-width: 100vw;
    min-height: 74px;
    padding-left: 14px;
    padding-right: 14px;
  }

  .logo {
    width: 38px;
    height: 38px;
  }

  .header__profile {
    max-width: 132px;
  }

  .header__registration {
    font-size: 10px;
    max-width: 64px;
    max-height: 16px;
    margin-right: 14px;
  }

  .header__login {
    min-width: 54px;
    min-height: 26px;
    font-size: 10px;

  }
}

@media screen and (min-width: 768px) {
  .header {
    min-height: 74px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .header__profile {
    max-width: 183px;
  }

  .header__registration {
    font-size: 12px;
    max-width: 77px;
    margin-right: 30px;
  }

  .header__login {
    font-size: 12px;
    min-width: 76px;
    max-height: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .header {
    min-height: 74px;
    padding-left: 70px;
    padding-right: 70px;
  }
}
