    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    z-index: 10;
    /*padding: 20px;*/
}

.preloader_close {
    display: none;
}

.preloader__container {
    width: 100px;
    height: 100px;
    position: relative;
}

.preloader__round {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
    background: #2BE080;
    border-radius: 50px;
}

.preloader__round::after,
.preloader__round::before {
    content: '';
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50px;
}

.preloader__round::after {
    background: #F5F5F5;
    -webkit-animation: preloader-inside-white 1s ease-in-out infinite;
            animation: preloader-inside-white 1s ease-in-out infinite;
}

.preloader__round::before {
    z-index: 10;
    background: #2BE080;
    -webkit-animation: preloader-inside-red 1s ease-in-out infinite;
            animation: preloader-inside-red 1s ease-in-out infinite;
}

